/* header style */
.header {
    min-height: 97px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.discord, .twitter, .mailbox {
    border: 3px solid;
    padding: 11px;
    border-radius: 50%;
    display: flex;
    width: max-content;
    height: fit-content;
}
.head-contact a:hover {
    transform: scale(1.1);
    transition: 0.3s;
}
.head-contact a {
    color: #000;
 }
.head-contact {
    display: flex;
}
.head-contact span {
    margin: 0 5px;
}
.logo img {
    width: 140px;
    height: 77px;
}

.connect_status {
    text-decoration: none;
    background-color: black;
    border-radius: 100px;
    color: #ffe14b;
    font-size: 20px;
    font-family: "Titan One", Sans-serif;
    padding: 15px 30px;
    cursor: pointer;
    transition: transform 0.5s;
}
.connect_status:hover {
    transform: scale(1.2);
    transition-timing-function: cubic-bezier(0.47, 2.02, .31, -.36);
}

.mainmenu {
    width: inherit;
    display: block;
    margin: 0 14px;
}
.desktopMenu ul {
    margin: 5px;
    display: flex;
    justify-content: center;
}
.desktopMenu li {
    display: inline;
    padding: 10px 10px;
    display: flex;
    align-items: center;
}
.desktopMenu li:not(:last-child):after {
    content: "";
    border-left: 2px dotted #000;
    height: 40%;
    margin-left: 20px;
}
.desktopMenu li a {
    text-decoration: none;
    font-size: 30px;
    font-family: "Titan One", Sans-serif;
    color: #000;
}
.desktopMenu a:hover{
    display: block;
    transform: scale(1.2);
    transition: transform 0.2s;
}
.arrow {
    width: 100%;
    height: 22px;
    background-image: url("../../image/arrows.svg");
    background-repeat: repeat-x;
}
@media (min-width: 769px) {
    .mobileMenu {
        display: none;
    }
    .desktopMenu ul {
        display: flex;
    }
}
@media (max-width: 1024px) {
    .head-contact {
      visibility: hidden;
    }
    .desktopMenu ul {
        flex-wrap: wrap;
    }
}

@media (max-width: 768px) {
    .head-contact {
      display: none;
    }
    .header {
        display: block;
        height: auto;
    }
    .logo img {
        width: 30%;
        height: auto;
        margin: 40px;
    }
    .connect_status{
        font-size: 15px;
        padding: 13px 30px;
        margin-bottom: 30px;
    }
    .top_arr {
        margin-bottom: 20px;
    }
    .bottom_arr {
        margin-top: 10px;
    }
    .mainmenu {
        margin: 0;
    }
    .desktopMenu ul {
        display: none;
    }
    .mobile_icon {
        background-color: #000;
        display: block;
        margin: auto;   
        width: fit-content;
        border-radius: 3px;
        padding: 5px 5px 2px 5px;
    }
    .mobile_ul {
        background-color: #000;
        overflow: hidden;
        transition: max-height 0.5s cubic-bezier(1, 0, 1, 0);
        height: auto;
        max-height: 9999px;
        margin-top: 10px;
    }
    .mobile_ul.collapsed {
        max-height: 0;
        transition: max-height 0.5s cubic-bezier(0, 1, 0, 1);
    }
    .mobile_ul ul {
        display: block !important;
        margin: 0px;
        padding: 0;
    }
    .mobile_ul li {
        display: block;
    }
    .mobile_ul li:hover {
        background-color: #55595c;
    }
    .mobile_ul li a {
        color: #ffe14b !important;
        border: none !important;
        font-size: 26px;
        font-family: "Titan One", Sans-serif;
        text-decoration: none;
    }
    
}

/* Mint style */
.mintTitle {
    font-size: 70px;
    font-family: "Titan One", Sans-serif;
    margin: 33px 0 -10px 0;
    color: #000;
}
.mintSubtitle {
    font-family: "Exo 2", Sans-serif;
    font-size: 20px;
    font-weight: 600;
    font-style: italic;
    color: #000;
    line-height: 22px;
    margin-bottom: 0;
}
.mintSubtitle1 {
    font-family: "Exo 2", Sans-serif;
    font-size: 20px;
    font-weight: 600;
    font-style: italic;
    color: #000;
    line-height: 22px;
    margin-bottom: 0;
    margin-top: 0;
}
.mint-image img {
    width: 100%;
}
.mint-count {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
.mint-count h2 {
    border: 3px solid #000;
    border-radius: 10px;
    font-family: "Exo 2", Sans-serif;
    font-style: italic;
    font-weight: bold;
    padding: 2px 17px;
    margin: 0 50px;
    font-size: 25px;
