}
.mint-count a {
    padding: 3px 15px;
    font-size: 25px;
    font-family: "Titan One", Sans-serif;
    font-weight: bold;
    background: #000;
    color: #ffe14b;
    border-radius: 12px;
    cursor: pointer;
}
.mint-count a:hover {
    background-color: #55595c;
    cursor: pointer;
}
.mint-btn {
    margin: 20px;
    cursor: pointer;
}
.mint-btn a {
    padding: 7px 30px;
    font-size: 25px;
    font-family: "Titan One", Sans-serif;
    font-weight: inherit;
    background: #000;
    color: #ffe14b;
    border-radius: 12px;
    cursor: pointer;
}
.mint-btn a:hover {
    background-color: #55595c;
    cursor: pointer;
}

@media (max-width: 768px) {
    .mintTitle {
        font-size: 39px;
    }
    .mintSubtitle, .mintSubtitle1{
        font-size: 15px;
    }
}

