/* cluckyverse style */

.cluckverseContainer {
    border-bottom: 3px solid #000;
    padding-bottom: 12px;
}
.verseBox {
    display: flex;
    position: relative;
}
.verse-title {
    position: relative;
}
.flow {
    position: absolute;
    left: 0;
    bottom: -15px;
    width: -webkit-fill-available;
    transform: rotate(180deg);
    margin: auto;
}
.verse-content {
    position: relative;
}
.verse-svg {
    position: absolute;
    width: -webkit-fill-available;
}
.verse-title h1 {
    font-family: "Titan One", Sans-serif;
    font-size: 50px;
    color: #000;
    margin: 0px;
    padding: 16px 0 30px 0;
}
.pixelVerse, .Verse2D, .Verse3D {
    width: 33%;
    padding: 10px;
}
.pixelVerse img:hover, .Verse2D img:hover, .Verse3D img:hover {
    transform: scale(0.9);
}
.pixelVerse img, .Verse2D img, .Verse3D img {
    width: 70%;
    transition-duration: 0.3s;
}
.pixelVerse h2, .Verse2D h2, .Verse3D h2 {
    font-family: "Titan One", Sans-serif;
    font-size: 25px;
    line-height: 1.4;
    font-weight: inherit;
    color: #000;
    margin-top: 0;
}
.pixelVerse p, .Verse2D p, .Verse3D p {
    font-family: "Exo 2", Sans-serif;
    font-size: 20px;
    font-weight: bold;
    color: #000;
    line-height: 1.8;
}

@media (max-width: 1000px) {
    .flow {
        bottom: -16px;
    }
}
@media (max-width: 768px) {
    .verseBox {
        display: block;
    }
    .verse-title h1 {
        font-size: 40px;
    }
    .pixelVerse, .Verse2D, .Verse3D {
        width: 95%;
        margin: 0 10px;
    }
    .flow {
        bottom: -1px;
    }
    .flow img {
        height: 18px;
    }
}

