 /* Accordion STYLE */
.accordion {
    margin: 10px;
    border-left: 3px solid #000;
    border-right: 3px solid #000;
    border-top: 3px solid #000;
}
.accordion-title {
    display: flex;
    justify-content: space-between;
    padding: 50px 30px;
    background: transparent;
    border-bottom: 3px solid #000;
    cursor: pointer;
    transition: 0.4s;
}
.accordionTitle {
    text-decoration: underline;
    font-family: "Titan One", Sans-serif;
    font-size: 30px;
    font-weight: 700;
    color: #000;
    line-height: 1;
    text-align: initial;
} 
.accordion-con-item {
    overflow: hidden;
    transition: max-height 0.5s cubic-bezier(1, 0, 1, 0);
    height: auto;
    max-height: 9999px;
}
  
.accordion-con-item.collapsed {
    max-height: 0;
    transition: max-height 0.5s cubic-bezier(0, 1, 0, 1);
}
.accordion-content {
    text-align: start;
    font-size: 22px;
    font-family: "Exo 2", Sans-serif;
    font-weight: bold;
    color: #000;
    padding: 30px 20px;
    background: transparent;
    border-bottom: 3px solid #000;
    line-height: 1.8;
}